

/* BASIC RESET */

*, *::before, *::after {
  box-sizing: border-box; 
}

* {
  margin: 0; 
}

html, body {
  height: 100%; 
  font-family: Helvetica, sans-serif;
}

html {
  scroll-behavior: smooth;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

#root, #__next {
  isolation: isolate;
}


/* TEXT STYLING */

h1 {
  font-size: 7rem;
  line-height: 6rem;
}

h2 {
  font-size: 5rem;
  line-height: 5.5rem;
}

h3 {
  font-size: 1.875rem;
  line-height: 2rem;
}

h4 {
  font-size: 1.35rem;
  line-height: 1.75rem;
}

h5 {
  font-size: 1.1rem;
  line-height: 1.35rem;
  font-weight: 500;
}

a, a:hover, a:active {
  color: black;
}

p {
  font-size: 1rem;
  line-height: 1.75rem;
}

p.grey {
  color: grey; 
}

p.light {
  color: #8c9dad;
}

/* ELEMENT STYLING */

ul {
  padding: 0;
}

li {
  list-style: none;
}

a {
  color: black;
  text-decoration: none;
}

button {
  font-size: .9rem;
  border-radius: .5rem;
  padding: .25rem .5rem;
  cursor: pointer;
}

  button.white {
    background: white;
    color: black;
    border: solid 1px white;
  }

    button.white:hover {
      background: black;
      color: white;
    }

    button.white-transparent {
      background: none;
      border: 1px solid white;
      color: white;
    }

    button.white-transparent:hover {
      background: white;
      color: black;
    }

  button.black {
    background: black;
    color: white;
    border: 1px solid black;
  }

    button.black:hover {
      background: white;
      color: black;
    }

    button.black-transparent {
      background: none;
      color: black;
      border: 1px solid black;
    }

    button.black-transparent:hover {
      background: black;
      color: white;
    }

  button.light {
    background: white;
    color: #8c9dad;
    border: 1px solid #8c9dad;
  }

    button.light:hover {
      background: white;
      color: black;
      border: 1px solid black;
    }

.container {
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
}


/* CUSTOM STYLING */

.inline {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.sticky-wrapper {
    position: -webkit-sticky;
    position: sticky;
    top: 0rem;
    z-index: 4;
    background: #1c1c1c;
    transition: background-color .25s;
}

    .sticky-wrapper * {
        color: white;
    }

    .sticky-wrapper-light {
        position: -webkit-sticky;
        position: sticky;
        top: 0rem;
        z-index: 4;
        background: white;
        transition: background-color .25s;
        box-shadow: rgba(33, 35, 38, 0.1) 0px 10px 10px -10px;
    } 

    .sticky-wrapper-light * {
        color: black;
    }

nav {
    display: inline-flex;
    align-items: center;
    padding: 1rem 3rem;
    width: 100%;
}

.brand {
    font-weight: bolder;
    font-size: 1.1rem;
}

.mobile-icon {
    display: none;
}

.nav-links {
    display: flex;
    flex-direction: row;
    margin-left: auto;
    font-size: .85rem;
}

.nav-links li {
    margin-left: 2rem;
}

.nav-links li:hover {
    opacity: 75%;
}

.nav-links button {
    border: none;
    background: none;
}


@media screen and (max-width: 645px) {

    nav {
        flex-direction: column;
        padding: 0;
    }

    .mobile-navbar {
        width: 100%;
        display: flex;
        align-items: center;   
    }

    .brand {
        font-size: 2.25rem;
        margin: .5rem 0;
    }

    .mobile-icon {
        display: block;
        margin-left: auto;
    }

    .mobile-icon button {
        background: none;
        border: none;
        font-size: 2rem;
    }

    ul.nav-links {
        display: none;
        flex-direction: column;
        grid-gap: 0px;
        gap: 0px;
        height: 100vh;
        width: 100%;
        justify-content: center;
        align-items: center;
        position: absolute;
        background: #1c1c1c;
    }

    ul.nav-links li {
        color: white;
        text-align: center;
        margin: 0;
    }

    ul.nav-links button {
        margin: .5rem 0;
        font-size: 2rem;
        color: white;
    }
}
.hero {
    height: 100vh;
    width: 100%;
    margin-top: -60px;
    background: #1C1C1C;
    color: white;
}

    .hero .container {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: end;
        align-self: end;
        height: 100vh;
    }

    .hero h1 {
        height: 100%;
        width: 100%;
        max-width: 20rem;
        position: absolute;
        left: 0px;
        display: flex;
        align-items: center;
        text-align: right;
    }

    .hero-image {
        position: absolute;
        width: 70vh;
        -webkit-filter: drop-shadow(0 -.5rem 0.5rem rgba(22, 22, 22, 0.5));
                filter: drop-shadow(0 -.5rem 0.5rem rgba(22, 22, 22, 0.5));
    }

    .hero-info {
        background: rgb(28, 28, 28, .75);
        width: 15rem;
        position: absolute;
        right: 3vw;
        bottom: 1rem;
        box-sizing: content-box;
        padding:1.5rem;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
        border-radius: .5rem;
    }

    .hero-buttons {
        display: flex;
        margin-top: 1rem;
        grid-column-gap: 10px;
        -webkit-column-gap: 10px;
                column-gap: 10px;
    }

    p.grey:first-of-type {
        font-weight: bold;
    }

    .hero h4 {
        margin: .25rem 0 .5rem 0;
    }

.about {
    margin: 5rem 0 7rem 0;
}

    .about-me {
        text-align: center;
        max-width: 600px;
        margin: 0 auto;
    }

    .about-me h3 {
        line-height: 2.5rem;
    }

    .about-me p {
        margin-top: 1rem;
    }


    .exp-banner {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin: 2.5rem 0 3.5rem 0;
    }

        .grey-box {
            flex-grow: 3;
            height: 60px;
            width: 100%;
            background: #f7f7f7;
        }

        .exp-item {
            padding: 1rem;
            display: flex;
            flex-grow: 1;
            flex-direction: row;
            justify-content: center;
            grid-gap: 5px;
            gap: 5px;
            min-width: 200px;
        }

        .exp-info {
            display: flex;
            flex-direction: column;
        }

        .exp-item p:first-of-type {
            font-size: 20px;
            line-height: 1.30rem;
            margin-top: 10px;
        }

        .exp-item p.light {
            font-size: 14px;
            width: 100%;
        }

    .tech-stack h4 {
        text-align: center;
        margin-bottom: 1rem;
    }

        .knowledge {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-column-gap: 20px;
            -webkit-column-gap: 20px;
                    column-gap: 20px;
        }

        .knowledge ul {
            max-width: 400px;
            width: 75%;
        }

        .knowledge ul:first-child {
            justify-self: end;
        }

.work {
    background: #f7f7f7;
    padding: 7rem 0;
    text-align: center;
}

    .work h3 {
        margin-bottom: 2rem;
    }

    .project-list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 25px;
        gap: 25px;
        width: 100%;
    }

.contact {
    margin: 7rem 0;
}

    .contact .container {
        display: flex;
        flex-direction: row;
        grid-gap: 25px;
        gap: 25px;
    }

    .contact .container img {
        width: 40%;
        height: auto;
    }

    .contact-info {
        width: 60%;
    }

    .contact-info p {
        margin: 1.5rem 0;
        max-width: 525px;
    }

    .contact-methods {
        display: inline-flex;
        grid-gap: 25px;
        gap: 25px;
    }

    .contact-methods > li {
        display: flex;
        align-items: center;
        grid-gap: 10px;
        gap: 10px;
        margin-bottom: 1rem;
    }

    .purple {
        color: #BD1DCB;
        font-size: 1.5rem;
    }

    .contact-methods button {
        border: none;
        background: none;
    }

    .contact-methods button:hover {
        opacity: 50%;
    }


    


/* responsive css */

@media screen and (max-width: 1000px) {

    .hero h1 {
        font-size: 20vw;
        line-height: 17vw;
        max-width: 100%;
        height: 50%;
        text-align: center;
        top: 3rem;
    }

    .hero-info {
        height: 13rem;
        left: 0;
        right: 0;
        margin: 0 auto;
        top: 85vh;
        background: #1c1c1c;
        text-align: center;
    }

    .hero-buttons {
        justify-content: center;
    }

    .about-me {
        margin-top: 12rem;
    }

    .knowledge ul {
        margin: 0 auto;
    }

    .project-list {
        grid-template-columns: repeat(2,1fr);
    }

    .contact .container {
        flex-direction: column;
    }

    .contact-info {
        width: 100%;
        text-align: center;
    }

    .contact-info p {
        margin: 1.5rem auto;
    }

    .contact .container img {
        display: none;
    }
}



@media screen and (max-width: 645px) {

    .hero {
        height: 100%;
        padding: 2rem 0;
    }

    .hero .container {
        flex-direction: column;
        grid-row-gap: 1rem;
        row-gap: 1rem;
        height: 100%;
    }

        .hero h1 {
            display: block;
            position: static;
            margin-top: 6rem;
            height: 140px;
            align-items: end;
        }

        .hero-image {
            position: static;
            top: 0;
            box-sizing: content-box;
            border-radius: 100%;
        }

        .hero-info {
            height: 100%;
            width: 80%;
            margin: 0 auto;
            position: static;
        }

        .hero-buttons {
            flex-direction: column;
            grid-row-gap: 10px;
            row-gap: 10px;
        }

        .hero-buttons button {
            font-size: 1.25em;
            width: 100%;
        }

    .about-me {
        margin-top: 0rem;
    }

    .knowledge {
        grid-template-columns: 1fr;
    }

    .knowledge ul {
        width: 100%;
    }

    .knowledge ul:first-child {
        justify-self: start;
    }
}

@media screen and (max-width: 600px) {

    .project-list {
        grid-template-columns: 1fr;
    }
    
    .contact-methods {
        display: flex;
        flex-direction: column;
        width: 15rem;
        margin: 0 auto;
        grid-gap: 0;
        gap: 0;
    }

    .contact-methods li {
        margin-left: 2rem;
    }

    .contact-methods button {
        font-size: 1.15rem;
    }
}
.project {
    width: 100%;
    background: #f7f7f7;
    text-align: center;
    border-radius: .5rem;
    display: flex;
    flex-direction: column;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
}

    .project img {
        width: 100%;
        height: 100%;
        background: lightgrey;
        border-radius: .25rem .25rem 0 0;
    }

    .project-info {
        background: white;
        padding: 2rem;
        border-radius: 0 0 .25rem .25rem;
    }

    .project-info .inline {
        grid-gap: 10px;
        gap: 10px;
        justify-content: center;
        margin-top: 1rem;
    }

    .project button {
        line-height: 1rem;
        padding: .5rem;
    }
.skill-container {
    height: .6rem;
    width: 100%;
    background: #f1f1f1;
    border: 2px solid #8c9dad;
    margin: 10px 0;
}

.skill-level {
    height: 100%;
    background: #8c9dad;
}


.sidebar-open {
    display: flex;
    flex-direction: column;
    max-width: 600px;
    height: 100vh;
    overflow: hidden;
    background-color: white;
    padding: 2rem;
    position: fixed;
    top: 0px;
    right: 0px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
    z-index: 5;
}

.sidebar-closed {
    display: none;
}

.sidebar-content {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    -ms-overflow-style: none; 
    scrollbar-width: none; 
    padding-bottom: 5rem;
}

    .sidebar-content::-webkit-scrollbar {
        display: none;
    }

    .close-button {
        display: flex;
        flex-direction: row;
        justify-content: end;
        width: 100%;
        grid-gap: 5px;
        gap: 5px;
        background: none;
        align-items: center;
        border: none;
    }

    .close-button p {
        padding: .10rem .75rem;
        border-radius: .5rem;
    }

    .close-button p:hover {
        background: #f1f1f1;
    }

    .sidebar-content .inline {
        margin: 1rem 0;
        grid-column-gap: 10px;
        -webkit-column-gap: 10px;
                column-gap: 10px;
    }

    .sidebar-content h3, .sidebar-content h4 {
        font-weight: 400;
    }

    .sidebar-content h4 {
        margin: 3rem 0 .75rem 0;
    }

    .project-tags {
        margin-top: 1rem;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        grid-gap: 10px;
        gap: 10px;
        max-width: 600px;
    }

    .project-tags li {
        background: #f7f7f7;
        padding: 0 .5rem;
        border-radius: .25rem;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;

    }
footer {
    background: #f7f7f7;
    color: #8c9dad;
    text-align: center;
    padding: 1rem 0;
}
